webpackJsonp([180747060961185],{

/***/ 455:
/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"JIGSAWYE","author":"Evan Ye","siteUrl":"https://jigsawye.com","disqusShortname":"jigsawnotes"}},"markdownRemark":{"id":"/Users/jigsawye/projects/gatsby-blog/content/posts/2016-02-01-setup-laravel-development-environment-with-homebrew.md absPath of file >>> MarkdownRemark","html":"<blockquote>\n<p>更新於 2017/1/10：Laravel 官方已推出 <a href=\"https://laravel.com/docs/5.3/valet\">Laravel Valet</a>，建議採用 Valet 方式進行安裝。</p>\n</blockquote>\n<blockquote>\n<p>建議你熟悉 Terminal 的操作再參考本篇，否則容易把系統環境搞亂。</p>\n</blockquote>\n<p>在開發 Laravel Application 時，新手可能使用 *AMP 的系統環境整合包，而大多數人會選擇官方所提供的 <a href=\"https://laravel.tw/docs/5.2/homestead\">Homestead</a>。但 Homestead 其實就是一包很大包的虛擬機再配上 Vargrant，曾幾何時我也是使用 Homestead，但是因為我的小 Macbook Air 實在是受不了虛擬機容量一大包，又吃一堆記憶體。</p>\n<p>雖然標題是寫 Laravel，但是要應用在其他的 PHP Application 也是可行的。本篇就以 Homebrew 為主軸，打造 Laravel 的開發環境。</p>\n<!-- more -->\n<h3 id=\"啥？homebrew\"><a href=\"#%E5%95%A5%EF%BC%9Fhomebrew\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>啥？Homebrew</h3>\n<p>如同 ubuntu 有 <code class=\"language-text\">apt-get</code>、CentOS 有 <code class=\"language-text\">yum</code> 一樣，<a href=\"http://brew.sh/index_zh-tw.html\">Homebrew</a> 就是 OSX 的套件管理器。不同的是前者都是系統內建的，而 Homebrew 則是因為 OSX 本身沒有這種東西，所以就有大神為 OSX 打造了套件管理器。</p>\n<h3 id=\"需要什麼？\"><a href=\"#%E9%9C%80%E8%A6%81%E4%BB%80%E9%BA%BC%EF%BC%9F\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>需要什麼？</h3>\n<p>在開始之前，先說說開發 Laravel 需要哪些需求？</p>\n<h6 id=\"php-cli\"><a href=\"#php-cli\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>PHP-CLI</h6>\n<p>Laravel 是目前在 PHP 中最熱門的 Framework，其實除了透過 *AMP 指向 <code class=\"language-text\">public</code> 資料夾及使用 Homestead 外，Laravel 也提供了 command line 的方式啟動 development server。</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ php artisan serve\nLaravel development server started on http://localhost:8000/</code></pre>\n      </div>\n<p>只要在專案目錄中執行 <code class=\"language-text\">php artisan serve</code>，就會執行 development server。也就是說我們至少需要 php-cli 來執行 development server。</p>\n<blockquote>\n<p>PHP-CLI 為 PHP Command Line Interface，也就是 Command Line 的 PHP 執行環境。</p>\n</blockquote>\n<h6 id=\"mariadb\"><a href=\"#mariadb\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>MariaDB</h6>\n<p>沒錯，開發一個 Web Application 一定需要資料庫，而我平常是使用 MariaDB，所以我們也必須安裝 MariaDB。</p>\n<h6 id=\"redis\"><a href=\"#redis\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Redis</h6>\n<p>而我平常在開發的時候，會把 Cache、Session、及 Queue 的 Driver 都設定為 Redis，所以我們會安裝 Redis。</p>\n<h6 id=\"總結\"><a href=\"#%E7%B8%BD%E7%B5%90\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>總結</h6>\n<p>所以我們必須透過 Homebrew 安裝這些東西：</p>\n<ul>\n<li>php</li>\n<li>mariadb</li>\n<li>redis</li>\n</ul>\n<h3 id=\"getting-started\"><a href=\"#getting-started\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Getting started</h3>\n<h6 id=\"homebrew\"><a href=\"#homebrew\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Homebrew</h6>\n<h5 id=\"安裝\"><a href=\"#%E5%AE%89%E8%A3%9D\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>安裝</h5>\n<p>開始之前，當然必須先安裝 <a href=\"http://brew.sh/index_zh-tw.html\">Homebrew</a>，官方網站已有提供指令進行安裝，打開 terminal 並輸入即可：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">/usr/bin/ruby -e <span class=\"token string\">\"<span class=\"token variable\"><span class=\"token variable\">$(</span><span class=\"token function\">curl</span> -fsSL https://raw.githubusercontent.com/Homebrew/install/master/install<span class=\"token variable\">)</span></span>\"</span></code></pre>\n      </div>\n<blockquote>\n<p>此指令會透過 curl 下載官方安裝檔，並透過 ruby 執行。他會在你的電腦安裝 homebrew。</p>\n</blockquote>\n<h5 id=\"使用\"><a href=\"#%E4%BD%BF%E7%94%A8\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>使用</h5>\n<p>Homebrew 的使用方式很簡單，以下介紹幾個常用指令：</p>\n<ul>\n<li><code class=\"language-text\">brew update</code>：更新 Homebrew。</li>\n<li><code class=\"language-text\">brew search wget</code>：透過 <code class=\"language-text\">brew search</code> 可以搜尋套件，範例為搜尋名字有 wget 的套件。</li>\n<li><code class=\"language-text\">brew install wget</code>：你可以使用 <code class=\"language-text\">brew intsall</code> 來安裝套件，範例會安裝 wget。</li>\n</ul>\n<p>更多指令可以輸入 <code class=\"language-text\">brew help</code>，或參考<a href=\"https://github.com/Homebrew/homebrew/blob/master/share/doc/homebrew/FAQ.md\">官方的 FAQ</a>。</p>\n<h6 id=\"php\"><a href=\"#php\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>PHP</h6>\n<h5 id=\"安裝-1\"><a href=\"#%E5%AE%89%E8%A3%9D-1\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>安裝</h5>\n<p>當然，我們首先必須安裝 PHP，我們先執行以下指令尋找 php：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ brew search php</code></pre>\n      </div>\n<p>你應該會看到非常多的 php，像是 <code class=\"language-text\">homebrew/php/php53</code>、<code class=\"language-text\">homebrew/php/php54</code>⋯⋯一直到最新的 <code class=\"language-text\">homebrew/php/php70</code>。而 php 7.0 有相容舊版本，所以我是使用 php 7.0 開發，所以我們來搜尋一下 php 7.0：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ brew search php70</code></pre>\n      </div>\n<p>你會看到 PHP 7.0，及 PHP 7.0 的 extenstions。我們直接安裝 php70 即可：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ brew <span class=\"token function\">install</span> php70</code></pre>\n      </div>\n<h5 id=\"使用-1\"><a href=\"#%E4%BD%BF%E7%94%A8-1\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>使用</h5>\n<p>安裝完後，執行以下指令看看是否有安裝完成：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ php -v\nPHP 7.0.2 <span class=\"token punctuation\">(</span>cli<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">(</span>built: Jan  7 2016 10:40:26<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">(</span> NTS <span class=\"token punctuation\">)</span>\nCopyright <span class=\"token punctuation\">(</span>c<span class=\"token punctuation\">)</span> 1997-2015 The PHP Group\nZend Engine v3.0.0, Copyright <span class=\"token punctuation\">(</span>c<span class=\"token punctuation\">)</span> 1998-2015 Zend Technologies</code></pre>\n      </div>\n<p>你可以進入一個 Laravel 專案，並執行 development server 看看：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ laravel new awesome_homebrew\n$ <span class=\"token function\">cd</span> awesome_homebrew\n$ php artisan serve\nLaravel development server started on http://localhost:8000/</code></pre>\n      </div>\n<p>你也可以指定 Port：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ php artisan serve --port<span class=\"token operator\">=</span>8080\nLaravel development server started on http://localhost:8080/</code></pre>\n      </div>\n<p>如果是一般的 PHP 專案，可以用以下方式開啟 development server：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ php -S localhost:8000 -t public\nPHP 7.0.2 Development Server started at Mon Feb  1 17:02:58 2016\nListening on http://localhost:8000\nDocument root is /Users/jigsawye/Projects/laravel/demo/public\nPress Ctrl-C to quit.</code></pre>\n      </div>\n<p>其中 <code class=\"language-text\">localhost:8000</code> 的部分是指你要開在哪個 Host，後面的 public 則是選擇你 <code class=\"language-text\">indxe.php</code> 的目錄，也就是 Application 進入點。</p>\n<h6 id=\"mariadb-1\"><a href=\"#mariadb-1\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>MariaDB</h6>\n<h5 id=\"安裝-2\"><a href=\"#%E5%AE%89%E8%A3%9D-2\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>安裝</h5>\n<p>安裝方式一樣，先搜尋再安裝：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ brew search mariadb\n$ brew <span class=\"token function\">install</span> mariadb</code></pre>\n      </div>\n<h5 id=\"使用-2\"><a href=\"#%E4%BD%BF%E7%94%A8-2\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>使用</h5>\n<p>安裝完成後，Homebrew 會提示你可以使用以下指令開啟 MySQL：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ mysql.server start\nStarting MySQL\n<span class=\"token keyword\">.</span> SUCCESS<span class=\"token operator\">!</span></code></pre>\n      </div>\n<p>然後你可以連接至你的 MySQL：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ mysql -uroot</code></pre>\n      </div>\n<h6 id=\"redis-1\"><a href=\"#redis-1\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Redis</h6>\n<h5 id=\"安裝-3\"><a href=\"#%E5%AE%89%E8%A3%9D-3\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>安裝</h5>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ brew search redis\n$ brew <span class=\"token function\">install</span> redis</code></pre>\n      </div>\n<h5 id=\"使用-3\"><a href=\"#%E4%BD%BF%E7%94%A8-3\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>使用</h5>\n<p>同樣的，Homebrew 提示使用以下指令開啟 Redis：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ redis-server /usr/local/etc/redis.conf\n5725:M 01 Feb 15:53:36.291 * Increased maximum number of <span class=\"token function\">open</span> files to 10032 <span class=\"token punctuation\">(</span>it was originally <span class=\"token keyword\">set</span> to 2560<span class=\"token punctuation\">)</span>.\n                _._\n           _.-`<span class=\"token variable\"><span class=\"token variable\">`</span>__ <span class=\"token string\">''</span>-._\n      _.-<span class=\"token variable\">`</span></span><span class=\"token variable\"><span class=\"token variable\">`</span>    <span class=\"token variable\">`</span></span><span class=\"token keyword\">.</span>  <span class=\"token variable\"><span class=\"token variable\">`</span>_.  <span class=\"token string\">''</span>-._           Redis 3.0.7 <span class=\"token punctuation\">(</span>00000000/0<span class=\"token punctuation\">)</span> 64 bit\n  .-<span class=\"token variable\">`</span></span><span class=\"token variable\"><span class=\"token variable\">`</span> .-<span class=\"token variable\">`</span></span>`<span class=\"token variable\"><span class=\"token variable\">`</span><span class=\"token keyword\">.</span>  <span class=\"token variable\">`</span></span>`<span class=\"token variable\"><span class=\"token variable\">`</span>\\/    _.,_ <span class=\"token string\">''</span>-._\n <span class=\"token punctuation\">(</span>    '      ,       .-<span class=\"token variable\">`</span></span>  <span class=\"token operator\">|</span> <span class=\"token variable\"><span class=\"token variable\">`</span>,    <span class=\"token punctuation\">)</span>     Running <span class=\"token keyword\">in</span> standalone mode\n <span class=\"token operator\">|</span><span class=\"token variable\">`</span></span>-._<span class=\"token variable\"><span class=\"token variable\">`</span>-<span class=\"token punctuation\">..</span>.-<span class=\"token variable\">`</span></span> __<span class=\"token punctuation\">..</span>.-.`<span class=\"token variable\"><span class=\"token variable\">`</span>-._<span class=\"token operator\">|</span>'<span class=\"token variable\">`</span></span> _.-<span class=\"token string\">'|     Port: 6379\n |    <span class=\"token variable\"><span class=\"token variable\">`</span>-._   <span class=\"token variable\">`</span></span>._    /     _.-'</span>    <span class=\"token operator\">|</span>     PID: 5725\n  <span class=\"token variable\"><span class=\"token variable\">`</span>-._    <span class=\"token variable\">`</span></span>-._  <span class=\"token variable\"><span class=\"token variable\">`</span>-./  _.-<span class=\"token string\">'    _.-'</span>\n <span class=\"token operator\">|</span><span class=\"token variable\">`</span></span>-._<span class=\"token variable\"><span class=\"token variable\">`</span>-._    <span class=\"token variable\">`</span></span>-.__.-'    _.-<span class=\"token string\">'_.-'</span><span class=\"token operator\">|</span>\n <span class=\"token operator\">|</span>    <span class=\"token variable\"><span class=\"token variable\">`</span>-._<span class=\"token variable\">`</span></span>-._        _.-<span class=\"token string\">'_.-'</span>    <span class=\"token operator\">|</span>           http://redis.io\n  <span class=\"token variable\"><span class=\"token variable\">`</span>-._    <span class=\"token variable\">`</span></span>-._<span class=\"token variable\"><span class=\"token variable\">`</span>-.__.-<span class=\"token string\">'_.-'</span>    _.-'\n <span class=\"token operator\">|</span><span class=\"token variable\">`</span></span>-._<span class=\"token variable\"><span class=\"token variable\">`</span>-._    <span class=\"token variable\">`</span></span>-.__.-<span class=\"token string\">'    _.-'</span>_.-'<span class=\"token operator\">|</span>\n <span class=\"token operator\">|</span>    <span class=\"token variable\"><span class=\"token variable\">`</span>-._<span class=\"token variable\">`</span></span>-._        _.-'_.-'    <span class=\"token operator\">|</span>\n  <span class=\"token variable\"><span class=\"token variable\">`</span>-._    <span class=\"token variable\">`</span></span>-._<span class=\"token variable\"><span class=\"token variable\">`</span>-.__.-<span class=\"token string\">'_.-'</span>    _.-'\n      <span class=\"token variable\">`</span></span>-._    <span class=\"token variable\"><span class=\"token variable\">`</span>-.__.-<span class=\"token string\">'    _.-'</span>\n          <span class=\"token variable\">`</span></span>-._        _.-<span class=\"token string\">'\n              `-.__.-'</span>\n\n5725:M 01 Feb 15:53:36.293 <span class=\"token comment\"># Server started, Redis version 3.0.7</span>\n5725:M 01 Feb 15:53:36.293 * DB loaded from disk: 0.000 seconds\n5725:M 01 Feb 15:53:36.293 * The server is now ready to accept connections on port 6379</code></pre>\n      </div>\n<p>不過你會發現，Redis 會佔用一個 terminal，相當不便。</p>\n<h6 id=\"homebrew-services\"><a href=\"#homebrew-services\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Homebrew Services</h6>\n<p>相信有些人可能會發現，在啟動 MariaDB 及 Redis 時，指令有些微的不同，沒有一個統一管理這些 services 的工具，像是 ubuntu 的 <code class=\"language-text\">service &lt;servicename&gt; start</code>。雖然 Homebrew 有提供 <code class=\"language-text\">launchctl</code> 來管理，不過操作起來實在是太麻煩了。其實 Homebrew 額外有提供一個 services 的管理工具，透過這個工具就可以統一管理所有的 services。</p>\n<h5 id=\"安裝-4\"><a href=\"#%E5%AE%89%E8%A3%9D-4\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>安裝</h5>\n<p>執行以下指令安裝：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ brew tap homebrew/services</code></pre>\n      </div>\n<p>如果你剛剛有透過指令啟動 MariaDB 與 Redis，現在我們先將它關閉。Redis 直接透過 <code class=\"language-text\">Ctrl + C</code> 關閉即可，MariaDB 請使用以下指令關閉：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ mysql.server stop\nShutting down MySQL\n<span class=\"token punctuation\">..</span>. SUCCESS<span class=\"token operator\">!</span></code></pre>\n      </div>\n<h5 id=\"使用-4\"><a href=\"#%E4%BD%BF%E7%94%A8-4\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>使用</h5>\n<p>使用以下指令可以查看目前所安裝的 services：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ brew services list\nName    Status  User Plist\nmariadb stopped\nphp70   stopped\nredis   stopped</code></pre>\n      </div>\n<p>很方便吧！接著我們來啟動 MariaDB 及 Redis：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ brew services start mariadb\n<span class=\"token operator\">==</span><span class=\"token operator\">></span> Successfully started <span class=\"token variable\"><span class=\"token variable\">`</span>mariadb<span class=\"token variable\">`</span></span> <span class=\"token punctuation\">(</span>label: homebrew.mxcl.mariadb<span class=\"token punctuation\">)</span>\n\n$ brew services start redis\n<span class=\"token operator\">==</span><span class=\"token operator\">></span> Successfully started <span class=\"token variable\"><span class=\"token variable\">`</span>redis<span class=\"token variable\">`</span></span> <span class=\"token punctuation\">(</span>label: homebrew.mxcl.redis<span class=\"token punctuation\">)</span></code></pre>\n      </div>\n<p>查看一下啟用之後的 services 列表：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ brew sercies list\nName    Status  User     Plist\nmariadb started jigsawye /Users/jigsawye/Library/LaunchAgents/homebrew.mxcl.mariadb.plist\nphp70   stopped\nredis   started jigsawye /Users/jigsawye/Library/LaunchAgents/homebrew.mxcl.redis.plist</code></pre>\n      </div>\n<p>若要關閉 servies，執行 <code class=\"language-text\">brew services stop &lt;servicename&gt;</code> 即可！</p>\n<h3 id=\"額外補充\"><a href=\"#%E9%A1%8D%E5%A4%96%E8%A3%9C%E5%85%85\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>額外補充</h3>\n<h6 id=\"多版本-php-切換\"><a href=\"#%E5%A4%9A%E7%89%88%E6%9C%AC-php-%E5%88%87%E6%8F%9B\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>多版本 PHP 切換</h6>\n<p>在一些專案可能需不同的 PHP 版本來執行，以下就說明如何接換多版本的 PHP。</p>\n<h5 id=\"安裝其他版本的-php\"><a href=\"#%E5%AE%89%E8%A3%9D%E5%85%B6%E4%BB%96%E7%89%88%E6%9C%AC%E7%9A%84-php\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>安裝其他版本的 PHP</h5>\n<p>像前面說明的一樣，我們可以直接安裝其他版本的 PHP：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ brew <span class=\"token function\">install</span> php56\nError: Cannot <span class=\"token function\">install</span> homebrew/php/php56 because conflicting formulae are installed.\n\n  php70: because different php versions <span class=\"token function\">install</span> the same binaries.\n\nPlease <span class=\"token variable\"><span class=\"token variable\">`</span>brew unlink php70<span class=\"token variable\">`</span></span> before continuing.\n\nUnlinking removes a formula's symlinks from /usr/local. You can\n<span class=\"token function\">link</span> the formula again after the <span class=\"token function\">install</span> finishes. You can --force this\ninstall, but the build may fail or cause obscure side-effects <span class=\"token keyword\">in</span> the\nresulting software.</code></pre>\n      </div>\n<p>你應該看到這個錯誤，他的意思是指你已經安裝了其他版本的 PHP，請 <code class=\"language-text\">unlink</code> 其他版本後再安裝。<code class=\"language-text\">unlink</code> 意指你必須將原本綁的 <code class=\"language-text\">php70</code> 給卸除掉（並非移除），再接著安裝。</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ brew unlink php70\n$ brew <span class=\"token function\">install</span> php56</code></pre>\n      </div>\n<p>接著就可以查看 PHP 版本，你應該切換成 PHP 5.6：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ php -v\nPHP 5.6.17 <span class=\"token punctuation\">(</span>cli<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">(</span>built: Jan  8 2016 10:27:48<span class=\"token punctuation\">)</span>\nCopyright <span class=\"token punctuation\">(</span>c<span class=\"token punctuation\">)</span> 1997-2015 The PHP Group\nZend Engine v2.6.0, Copyright <span class=\"token punctuation\">(</span>c<span class=\"token punctuation\">)</span> 1998-2015 Zend Technologies</code></pre>\n      </div>\n<h5 id=\"切換回-php-70\"><a href=\"#%E5%88%87%E6%8F%9B%E5%9B%9E-php-70\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>切換回 PHP 7.0</h5>\n<p>若要切換回去也相當簡單，<code class=\"language-text\">unlink</code> PHP 5.6 再 <code class=\"language-text\">link</code> PHP 7.0 即可：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ brew unlink php56\nUnlinking /usr/local/Cellar/php56/5.6.17<span class=\"token punctuation\">..</span>. 18 symlinks removed\n$ brew <span class=\"token function\">link</span> php70\nLinking /usr/local/Cellar/php70/7.0.2<span class=\"token punctuation\">..</span>. 17 symlinks created\n$ php -v\nPHP 7.0.2 <span class=\"token punctuation\">(</span>cli<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">(</span>built: Jan  7 2016 10:40:26<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">(</span> NTS <span class=\"token punctuation\">)</span>\nCopyright <span class=\"token punctuation\">(</span>c<span class=\"token punctuation\">)</span> 1997-2015 The PHP Group\nZend Engine v3.0.0, Copyright <span class=\"token punctuation\">(</span>c<span class=\"token punctuation\">)</span> 1998-2015 Zend Technologies</code></pre>\n      </div>\n<h6 id=\"php-extenstions\"><a href=\"#php-extenstions\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>PHP extenstions</h6>\n<p>有時你可能需要安裝 PHP 的 extenstions，像是為了產生 PHPUnit 的 coverage report 就必須安裝 Xdebug，為了提升執行速度則裝 OPCache。</p>\n<h5 id=\"安裝-extenstions\"><a href=\"#%E5%AE%89%E8%A3%9D-extenstions\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>安裝 extenstions</h5>\n<p>所有的 PHP extenstions 都是以 <code class=\"language-text\">phpXX-*</code> 來命名，也就是說如果你現在使用 PHP 7.0，那麼就會是 <code class=\"language-text\">php70-*</code>。讓我們來搜尋 PHP 7.0 有哪些 extenstions 可以安裝：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ brew search php70\nhomebrew/php/php70                  homebrew/php/php70-imagick          homebrew/php/php70-mongodb          homebrew/php/php70-pthreads\nhomebrew/php/php70-amqp             homebrew/php/php70-intl             homebrew/php/php70-msgpack          homebrew/php/php70-rdkafka\nhomebrew/php/php70-apcu             homebrew/php/php70-kafka            homebrew/php/php70-mustache         homebrew/php/php70-redis\nhomebrew/php/php70-apcu-bc          homebrew/php/php70-libsodium        homebrew/php/php70-oauth            homebrew/php/php70-snmp\nhomebrew/php/php70-ast              homebrew/php/php70-lzf              homebrew/php/php70-opcache          homebrew/php/php70-tidy\nhomebrew/php/php70-blitz            homebrew/php/php70-mailparse        homebrew/php/php70-pcntl            homebrew/php/php70-uuid\nhomebrew/php/php70-ev               homebrew/php/php70-mcrypt           homebrew/php/php70-pdo-dblib        homebrew/php/php70-v8js\nhomebrew/php/php70-gmp              homebrew/php/php70-mecab            homebrew/php/php70-pdo-pgsql        homebrew/php/php70-xdebug\nhomebrew/php/php70-hprose           homebrew/php/php70-memcached        homebrew/php/php70-pspell           homebrew/php/php70-yaml</code></pre>\n      </div>\n<p>你會看到一拖車的 extenstions，這邊我以 Xdebug 為例：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ brew <span class=\"token function\">install</span> php70-xdebug</code></pre>\n      </div>\n<p>然後看看你的 PHP 版本，Xdebug 應該已經安裝完成：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ php -v\nPHP 7.0.2 <span class=\"token punctuation\">(</span>cli<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">(</span>built: Jan  7 2016 10:40:26<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">(</span> NTS <span class=\"token punctuation\">)</span>\nCopyright <span class=\"token punctuation\">(</span>c<span class=\"token punctuation\">)</span> 1997-2015 The PHP Group\nZend Engine v3.0.0, Copyright <span class=\"token punctuation\">(</span>c<span class=\"token punctuation\">)</span> 1998-2015 Zend Technologies\n    with Xdebug v2.4.0RC3, Copyright <span class=\"token punctuation\">(</span>c<span class=\"token punctuation\">)</span> 2002-2015, by Derick Rethans</code></pre>\n      </div>\n<h5 id=\"關閉-extenstions\"><a href=\"#%E9%97%9C%E9%96%89-extenstions\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>關閉 extenstions</h5>\n<p>所有的 extenstions 啟用檔案都會存放在 <code class=\"language-text\">/usr/local/etc/php/X.X/conf.d/ext-*.ini</code>，以剛剛安裝的 Xdebug 為例，他的檔案會存放在 <code class=\"language-text\">/usr/local/etc/php/7.0/conf.d/ext-xdebug.ini</code>。若要關閉該 extenstions，用編輯器編輯他即可：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\"><span class=\"token function\">sudo</span> vim /usr/local/etc/php/7.0/conf.d/ext-xdebug.ini</code></pre>\n      </div>\n<p>檔案內容很簡單：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\"><span class=\"token punctuation\">[</span>xdebug<span class=\"token punctuation\">]</span>\nzend_extension<span class=\"token operator\">=</span><span class=\"token string\">\"/usr/local/opt/php70-xdebug/xdebug.so\"</span></code></pre>\n      </div>\n<p>若要關閉，把第二行註解掉在存檔即可：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\"><span class=\"token punctuation\">[</span>xdebug<span class=\"token punctuation\">]</span>\n<span class=\"token comment\"># zend_extension=\"/usr/local/opt/php70-xdebug/xdebug.so\"</span></code></pre>\n      </div>\n<p>註解完後就關閉了：</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ php -v\nPHP 7.0.2 <span class=\"token punctuation\">(</span>cli<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">(</span>built: Jan  7 2016 10:40:26<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">(</span> NTS <span class=\"token punctuation\">)</span>\nCopyright <span class=\"token punctuation\">(</span>c<span class=\"token punctuation\">)</span> 1997-2015 The PHP Group\nZend Engine v3.0.0, Copyright <span class=\"token punctuation\">(</span>c<span class=\"token punctuation\">)</span> 1998-2015 Zend Technologies</code></pre>\n      </div>\n<h3 id=\"done！\"><a href=\"#done%EF%BC%81\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Done！</h3>\n<p>到此就完成囉，其實安裝起來不會很困難，操作起來也很簡單。相比 *AMP 與 Homestead，使用的資源及容量也輕巧很多。希望本篇可以提供給操作 terminal 一段時間的人做參考，畢竟自幹環境比起一鍵安裝包安心多了 XD，想裝就裝，想砍就砍！</p>","fields":{"slug":"/2016/02/01/setup-laravel-development-environment-with-homebrew/"},"frontmatter":{"title":"在 OSX 使用 Homebrew 打造精巧的 Laravel 開發環境","date":"February 01, 2016"}}},"pathContext":{"slug":"/2016/02/01/setup-laravel-development-environment-with-homebrew/"}}

/***/ })

});


// WEBPACK FOOTER //
// path---2016-02-01-setup-laravel-development-environment-with-homebrew-a75296ec14647762f09e.js